<template>
    <div>
        <div class="bg" style="background: url(static/img/others/bg.jpg) no-repeat center center">
        </div>
        <grid-cell :pages="pages"></grid-cell>
    </div>
</template>

<script>
import GridCell from '../../components/GridCell';
import localjson from '../../util/localjson';

export default {
    name: 'others',
    components: {
        GridCell,
    },
    data() {
        return {
            pages:[
                { index:1, title: '码头泊位', image: 'others/mtbw.png', path: 'others/mtbw', isValid: true },
                { index:2, title: '拖轮资料', image: 'others/tlzl.png', path: 'others/tlzl', isValid: false },
                { index:3, title: '潮汐数据', image: 'others/cxsj.png', path: 'others/cxsj', isValid: false },
                { index:4, title: '海区资料', image: 'others/hqzl.png', path: 'others/hqzl', isValid: false },
                { index:5, title: '交流互动', image: 'others/jlhd.png', path: 'others/jlhd', isValid: false },
                { index:6, title: '其他资料', image: 'others/qtzl.png', path: 'others/qtzl', isValid: false },
            ],
        };
    },
    methods: {
    },
    activated() {
        const currpage = {text: '其他资料', route: '/others'};
        localjson.setCurrentPage(currpage);
        this.$store.commit('setCurrentPage', currpage);

        this.$store.commit('setAppHeader', {
            show: true,
            title: '其他资料',
            showMenu: true,
            showBack: false,
            actions: [
            ]
        });
        this.$store.commit('setEnableSwipeOut', true);

    }
};
</script>

<style lang="stylus" scoped>
    @import '~@/assets/styles/variables';
    @import '~vuetify/src/stylus/settings/_colors';

    $theme := {
        primary: rgb(40, 116, 240)
        accent: $blue.accent-2
        secondary: $grey.darken-3
        info: $blue.base
        warning: $amber.base
        error: $red.accent-2
        success: $green.base
    }

    $material-custom := {
        bg-color: #fff
        fg-color: #000
        text-color: #000
        primary-text-percent: 0.87
        secondary-text-percent: 0.54
        disabledORhints-text-percent: 0.38
        divider-percent: 0.12
        active-icon-percent: 0.54
        inactive-icon-percent: 0.38
    }
    $material-theme := $material-custom

.bg
    
    margin 0 auto
    width 100%
    height 180px

</style>



